


// .desktop.margin(top,)

.desktop\.margin\(x\,auto\){
  margin-left: auto;
  margin-right: auto;
}

.desktop\.margin\(left\,auto\){
  margin-left: auto;
}

.desktop\.margin\(right\,auto\){
  margin-right: auto;
}

@for $i from 0 through 100 {
  .desktop\.margin\(top\,#{$i * 5}\){
    margin-top: #{$i * 5}px;
  }

  .desktop\.margin\(bottom\,#{$i * 5}\){
    margin-bottom: #{$i * 5}px;
  }
}


@for $i from 0 through 20 {

  .desktop\.padding\(all\,#{$i * 5}\){
    padding: #{$i * 5}px;
  }

  .desktop\.padding\(x\,#{$i * 5}\){
    padding-left: #{$i * 5}px;
    padding-right: #{$i * 5}px;
  }
  
  .desktop\.padding\(y\,#{$i * 5}\){
    padding-top: #{$i * 5}px;
    padding-bottom: #{$i * 5}px;
  }

  .desktop\.padding\(top\,#{$i * 5}\){
    padding-top: #{$i * 5}px;
  }

  .desktop\.padding\(bottom\,#{$i * 5}\){
    padding-bottom: #{$i * 5}px;
  }

  .desktop\.padding\(left\,#{$i * 5}\){
    padding-left: #{$i * 5}px;
  }

  .desktop\.padding\(right\,#{$i * 5}\){
    padding-right: #{$i * 5}px;
  }

  .desktop\.margin\(all\,#{$i * 5}\){
    margin: #{$i * 5}px;
  }

  .desktop\.margin\(x\,#{$i * 5}\){
    margin-left: #{$i * 5}px;
    margin-right: #{$i * 5}px;
  }
  
  .desktop\.margin\(y\,#{$i * 5}\){
    margin-top: #{$i * 5}px;
    margin-bottom: #{$i * 5}px;
  }

  .desktop\.margin\(left\,#{$i * 5}\){
    margin-left: #{$i * 5}px;
  }

  .desktop\.margin\(right\,#{$i * 5}\){
    margin-right: #{$i * 5}px;
  }


}